.wrapper {
  background-color: #333333;
  text-align: center;
  width: 100%;
  min-height: auto;
  justify-content: center;
  display: flex;
  align-items: center;
  /* 1d1d1d-first 333333-second 555555-third */
}

.container {
  text-align: center;
  width: 100%;
  min-height: auto;
  justify-content: center;
  display: flex;
  align-items: center;
  padding: 50px;
  margin: 50px;
  border-radius: 50px;
  background: #333333;
  box-shadow: 30px 30px 82px #1d1d1d, -30px -30px 59px #555555;
}

